const container = document.getElementById('root');
const ajax = new XMLHttpRequest();
const content = document.createElement('div');
// ë³€ê²½ì˜ ì—¬ì§€ê°€ ìˆëŠ” ê²ƒì€ ë³€ìˆ˜ë¡œ ë¹¼ë‘ëŠ” ê²ƒì´ ì¢‹ë‹¤ (data)
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json';

const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json';

function getData(url) {
  ajax.open('GET', url, false);
  // data ê°€ì ¸ì˜´
  ajax.send();

  return JSON.parse(ajax.response);
}

function newsFeed() {
  // data ì²˜ë¦¬(response to Object)
  const newsFeed = getData(NEWS_URL);

  const newsList = [];

  newsList.push('<ul>');
  for (let i = 0; i < 10; i++) {

    // ì•„ì´ëŸ¬ë‹ˆí•˜ê²Œë„ DOM ì‚¬ìš©ì— ëŒ€í•œ ì§ê´€ì„± ê²°ì—¬ì˜ í•´ê²°ì€ DOM ì‚¬ìš©ì„ ìì œí•˜ëŠ”ê²ƒ. (ë¬¸ìì—´ì„ ì´ìš©í•˜ì!)
    // ì„¤ë ¹ ì–‘ì´ ì¢€ ëŠ˜ì–´ë‚˜ê²Œ ë˜ë”ë¼ë„ ê°€ë…ì„±ì´ ì¢‹ì€ ê²ƒì´ ì¢‹ì€ ë“¯ í•˜ë‹¤.
    newsList.push(`
  <li>
    <a href="#${newsFeed[i].id}">
      ${newsFeed[i].title} (${newsFeed[i].comments_count})
    </a>
  </li>
  `);
  }

  newsList.push('</ul>');

  container.innerHTML = newsList.join('');
}

function newsDetail() {
  const id = location.hash.substr(1);

  const newsContent = getData(CONTENT_URL.replace('@id', id));
  const title = document.createElement('h1');

  container.innerHTML = `
    <h1>${newsContent.title}</h1>

    <div>
      <a href="#">ëª©ë¡ìœ¼ë¡œ</a>
    </div>
  `;

  title.innerHTML = newsContent.title;
  content.appendChild(title);
  // console.log(newsContent);
}

function router() {
  const routePath = location.hash;

  if (routePath === '') {
    newsFeed();
  } else {
    newsDetail();
  }
}

window.addEventListener('hashchange', router);

router();




